.more-button {
  display: flex;
  font: inherit;
  border: none;
  background-color: transparent;
  background-image: url('../../img/more.svg');
  background-repeat: no-repeat;
  padding: 0;
  padding-left: 30px;
  cursor: pointer;
}


@media (min-width: $tablet-width) {
  .more-button {
    margin-bottom: 0;
  }
}

.more-button--less {
  background-image: url('../../img/less.svg');
}
